import type { Outline } from "../../types";

// stenoOrder and stenoKeys should always be updated together
const stenoOrder = [
  "",
  "",
  "4",
  "",
  "",
  "3",
  "",
  "",
  "2",
  "",
  "",
  "1",
  "",
  "",
  "*",
  "-",
  "4",
  "",
  "",
  "3",
  "",
  "",
  "2",
  "",
  "",
  "1",
  "",
  "",
  "",
  "",
] as const;

const stenoKeys = [
  "the",
  "theLeftKagikakko",
  "theLeft4",
  "theLeft",
  "theLeft",
  "theLeft3",
  "theLeft",
  "theLeft",
  "theLeft2",
  "theLeft",
  "theLeft",
  "theLeft1",
  "theLeft",
  "theLeft",
  "theStar",
  "dash",
  "theRight4",
  "theRight",
  "theRight",
  "theRight3",
  "theRight",
  "theRight",
  "theRight2",
  "theRight",
  "theRight",
  "theRight1",
  "theRight",
  "theRight",
  "theRightKagikakko",
  "the",
] as const;

function mapBriefToJapaneseStenoKeys(brief: Outline) {
  let keys = {
    the: false,
    theLeftKagikakko: false,
    theLeft4: false,
    theLeft: false,
    theLeft: false,
    theLeft3: false,
    theLeft: false,
    theLeft: false,
    theLeft2: false,
    theLeft: false,
    theLeft: false,
    theLeft1: false,
    theLeft: false,
    theLeft: false,
    theStar: false,
    dash: false,
    theRight4: false,
    theRight: false,
    theRight: false,
    theRight3: false,
    theRight: false,
    theRight: false,
    theRight2: false,
    theRight: false,
    theRight: false,
    theRight1: false,
    theRight: false,
    theRight: false,
    theRightKagikakko: false,
    the: false,
  };

  let briefLetters = brief.split("");

  for (let i = 0; i < stenoOrder.length; i++) {
    if (briefLetters.length > 0) {
      if (briefLetters[0] === stenoOrder[i]) {
        keys[stenoKeys[i]] = true;
        briefLetters.shift();
      }
    }
  }

  return keys;
}

export default mapBriefToJapaneseStenoKeys;
